'use server';

import { ai } from '@/ai/genkit';
import { MessageData, Part } from 'genkit';
import { z } from 'zod';
import { knowledgeBase } from '@/lib/knowledge-base';

const ChatInputSchema = z.object({
  history: z.array(z.any()),
});

export async function chat(input: z.infer<typeof ChatInputSchema>) {
  try {
    const { history } = input;

    const systemPrompt = `You are a friendly and helpful chatbot assistant for an application called "My Abacus Pro".
    Your goal is to assist users with any questions they have about the app, abacus techniques, mental math, or their account.
    The app helps users practice mental arithmetic using abacus methods.
    Be concise and encouraging in your responses. If you don't know an answer, say so politely.

    Use the following knowledge base to answer questions. Do not make up information.

    <KnowledgeBase>
    ${knowledgeBase}
    </KnowledgeBase>
    `;

    const { stream } = ai.generate({
      prompt: history.map(h => ({ content: h.content })), // Pass the content array directly
      system: systemPrompt,
      model: 'googleai/gemini-2.0-flash',
      stream: true,
    });
    
    if (!stream) {
      throw new Error('Failed to create stream');
    }
    
    return stream;
  } catch (error) {
    console.error('Chat function error:', error);
    
    // Create a stream that yields an error message
    async function* createErrorStream() {
      yield {
        role: 'model',
        content: [{ text: `Sorry, I encountered an error. Please check the server console.` }]
      };
    }
    
    return createErrorStream();
  }
}
